.container {
  position: relative;
  padding-left: 8px;
  padding-right: 8px;
  font-size: 14px;
  //   color: #b3b3b3;
}

.item {
  display: flex;
  align-items: center;
  padding-bottom: 4px;
  height: 30px;
  box-sizing: border-box;
  border-radius: 4px;
  padding-left: 8px;
}

.item:hover {
  background-color: #f6f6f6;
}

.text {
  margin: 0;
  font-size: 14px;
}

.image {
  margin-right: 8px;
}

.item.selected {
  background-color: #dff2f9;
}

.screen {
  flex: 1;
  display: flex;
  justify-content: space-between;
  height: 30px;
  margin-bottom: 4px;
  margin-top: 4px;
  font-weight: bold;
  align-items: center;
  .title {
    display: flex;
    align-items: center;
    font-size: 16px;
    .selected {
      font-weight: bold;
    }
    .sep {
      margin-right: 6px;
      margin-left: 6px;
    }
  }

  .addBtn {
    width: 24px;
    cursor: pointer;
    text-align: center;
  }

  .ant-select-selector {
    padding: 0;
  }
}
