.header {
  height: 50px;
  display: flex;
  align-items: center;
  font-size: 16px;
  background-color: #fff;
  border-bottom: 1px solid #eee;
  justify-content: space-between;
}

.left {
  display: flex;
  align-items: center;
  width: 200px;
}

.project {
  // margin-left: 4px;
