  .projectName {
    font-size: 14px;
    font-weight: bold;
    color: #444;
    border: 1px solid #ddd;
    background-color: #f9f9f9;
    padding: 4px;
    border-radius: 4px;
    display: none;
  }

  .input {
    width: 240px;
    text-align: center;
  }
