}

.play {
  width: 30px;
  height: 30px;
  // background-color: #f9f9f9;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #ebebeb;
  padding-left: 2px;
  margin-right: 10px;
}

.logo {
  margin-left: 10px;
}

.actions {
  display: flex;
  align-items: center;
  padding-right: 10px;
  width: 200px;
  box-sizing: border-box;
  justify-content: end;
}

.more {
  width: 30px;
  height: 30px;
  background-color: #f9f9f9;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #ebebeb;
}

.share {
  margin-right: 10px;
}
