.App-main {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100%;

}

.App-widgets {
 width: 250px;
 height: 100%;
}

.App-widgets-list {
  padding-top: 10px;
  padding-left: 8px;
  padding-right: 8px;
  box-sizing: border-box;
  overflow: hidden;
  border-bottom: 1px solid #ddd;
}

.App-widgets-item {
  float: left;
  width: 60px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center; 
  margin-bottom: 10px;
  margin-right: 8px;
  margin-left: 8px;
  box-sizing: border-box;
  font-size: 14px;
}

.App-widgets-item-img {
  width: 100%;
  background: #f9f9f9;
  border-radius: 4px;
  border: 1px solid #ebebeb;
  margin-bottom: 4px;
  padding: 4px;
  box-sizing: border-box;

}

.App-widgets-item-img:hover {
  border-color: #c7c7c7
}

.App-widgets-item-clone ~ .App-widgets-item {
  transform: none!important;
}

.App-widgets-item-container {
  height: 120px;
  overflow: hidden;
  position: relative;
  transform: none!important;
  margin-bottom: 20px;
}

.App-widgets-item-dragging {
  width: 360px!important;
}


.App-stage {
  flex: 1;
  min-width: 100px;
  background-color: #f5f5f5;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 10;
  overflow: hidden;
}

.App-settings {
  width: 300px;
  height: 100%;
  display: none;
}

.App-screen {
  margin-left: auto;
  margin-right: auto;
  height: 640px;
  width: 360px;
  position: relative;
  background-color: #fff;
  position: relative;
  z-index: 10;

}

.absolute-container {
  height: 200px;
  background-color: #f9f9f9;
}
